.post-type-archive .site__shop,
.tax-product_cat .site__shop,
.tax-product_tag .site__shop,
.single-product .archive-header .woocommerce-breadcrumb,
.related.products,
.upsells.products {
    @extend .container !optional;
}


.below-content {
    clear: both;
    overflow: hidden;
    padding-top: 2rem;
    padding-bottom: 1.7em;
    border-top: 1px solid #eee
}

.below-content .col-full, footer .col-full {
    display: flex;
    padding-right: calc(2.617924em - 20px);
    padding-left: calc(2.617924em - 20px)
}

.below-content .widget .widget-title {
    margin-bottom: 0;
    padding-left: 32px;
    font-size: 15px;
    font-weight: 600;
    line-height: 1.35;
    text-transform: none;
    letter-spacing: 0
}

.below-content .widget p {
    margin-bottom: 0;
    padding-left: 32px;
    color: #777;
    font-size: 14px
}

.below-content .widget {
    position: relative;
    margin: 0;
    padding: 0 20px;
    flex: 1
}

.below-content .widget .ri {
    position: absolute;
    top: 3px;
    left: 20px;
    color: #999;
    font-size: 16px
}


.site__banner{
    background-color: var(--wprs-bg-color);
    background-image: var(--wprs-bg-image);
    min-height: 300px;
    @apply flex flex-col justify-center items-center bg-cover bg-center bg-no-repeat;

    h1,p,span,a{
        color: var(--wprs-text-color);
    }
}
